import React from 'react'
import PropTypes from 'prop-types'
import DSInput from '@elliemae/ds-basic/form/Input'

const DSTextBox = ({
  containerProps = {},
  autoFocus = false,
  disabled = false,
  maxLength = 255,
  minLength = 0,
  fluidWidth = false,
  placeholder,
  value = '',
  hasError = false,
  readOnly = false,
  innerRef = null,
  type = 'text',
  min = undefined,
  max = undefined,
  clearable = false,
  leftComponent = null,
  rightComponent = null,
  required = false,
  variant,
  ...otherProps
}) => {
  return (
    <DSInput
      {...otherProps}
      aria-label={otherProps['aria-label'] || 'Text Box'}
      autoFocus={autoFocus}
      clearable={clearable}
      containerProps={containerProps}
      disabled={disabled}
      fluidWidth={fluidWidth}
      hasError={hasError}
      innerRef={innerRef}
      leftComponent={leftComponent}
      max={max}
      maxLength={maxLength}
      min={min}
      minLength={minLength}
      placeholder={placeholder}
      readOnly={readOnly}
      required={required}
      rightComponent={rightComponent}
      role='textbox'
      type={type}
      value={value}
      variant={variant}
    />
  )
}


DSTextBox.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Whether the text box uses auto focus or not
   */
  autoFocus: PropTypes.bool,
  /**
   * Whether the text box is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * Max length for the text box
   */
  maxLength: PropTypes.number,
  /**
   * Min length for the text box
   */
  minLength: PropTypes.number,
  /**
   * Make the content of the text box fit the container or not
   */
  fluidWidth: PropTypes.bool,
  /**
   * Placeholder for the text box
   */
  placeholder: PropTypes.string,
  /**
   * Default value once the component is initialized
   */
  value: PropTypes.string,
  /**
   * Whether the text box has error or not
   */
  hasError: PropTypes.bool,
  /**
   * Whether the text box is read only or not
   */
  readOnly: PropTypes.bool,
  min: PropTypes.number,
  max: PropTypes.number,
  /**
   * Whether the text box is clearable or not
   */
  clearable: PropTypes.bool,
  /**
   * Component to be added at the left side of the text box
   */
  leftComponent: PropTypes.element,
  /**
   * Component to be added at the right side of the text box
   */
  rightComponent: PropTypes.element,
  /**
   * Whether the text box is required or not
   */
  required: PropTypes.bool,
  /**
   * [
   *  'variant-default',
   *  'variant-focus',
   *  'variant-active',
   *  'variant-disabled',
   * ]
   */
  variant: PropTypes.string,
}

export default DSTextBox
