import React from 'react'
import PropTypes from 'prop-types'
import { aggregatedClasses, convertPropToCssClassName } from '@elliemae/ds-classnames'
import DSPopper, { PopperPositions, PopperInteractions } from '@elliemae/ds-basic/Popper'
import getComponentFromProps from '@elliemae/ds-utilities/getComponentFromProps'
import {
  DEFAULT_DELAY_OPEN,
  DEFAULT_DELAY_CLOSE,
} from '@elliemae/ds-shared/constants'
import { TooltipType } from './TooltipType'
import { interactions } from '../Popper/interaction'
import { positions } from '../Popper/positions'


const blockName = 'tooltip'

const TooltipContainer = aggregatedClasses('div')(blockName)
const TooltipText = aggregatedClasses('span')(blockName, 'text')

const DSTooltip = ({
  containerProps = {},
  innerRef,
  placement = PopperPositions.TOP,
  title = '',
  delayClose = DEFAULT_DELAY_CLOSE,
  delayOpen = DEFAULT_DELAY_OPEN,
  interactionType = PopperInteractions.HOVER,
  triggerComponent = null,
  className = '',
  isOpen = undefined,
  onOpen = () => null,
  springAnimationComponent = undefined,
  tooltipType = TooltipType.TOOLTIP,
  showArrow = true,
  zIndex,
  ...otherTooltipProps
}) => {
  const { cssClassName: tooltipBlockName } = convertPropToCssClassName(blockName)

  let Content = title
  if (typeof title === 'string') {
    Content = (
      <TooltipText>
        {title}
      </TooltipText>
    )
  }

  return (
    <DSPopper
      blockName={tooltipBlockName}
      contentComponent={(
        <TooltipContainer
          {...containerProps}
          {...otherTooltipProps}
          className={`tooltip-container--${tooltipType} ${className}`}
          innerRef={innerRef}
        >
          {Content}
        </TooltipContainer>
      )}
      delayClose={delayClose}
      delayOpen={delayOpen}
      interactionType={interactionType}
      isOpen={isOpen}
      onOpen={onOpen}
      placement={placement}
      showArrow={tooltipType === TooltipType.TOOLTIP ? showArrow : false}
      springAnimationComponent={springAnimationComponent}
      triggerComponent={getComponentFromProps(triggerComponent)}
      zIndex={zIndex}
    />
  )
}

DSTooltip.propTypes = {
  containerProps: PropTypes.shape({}),
  /** Tooltip position */
  placement: PropTypes.oneOf(positions),
  /** Tooltip content */
  title: PropTypes.string,
  /** Delay to close the tooltip */
  delayClose: PropTypes.string,
  /** Delay to open the tooltip */
  delayOpen: PropTypes.string,
  /** A type indicating how to open/close the tooltip */
  interactionType: PropTypes.oneOf(interactions),
  /** The component that will trigger the open/close interaction */
  triggerComponent: PropTypes.element,
  /** Whether the tooltip is visible or not */
  isOpen: PropTypes.bool,
  /** Handler when the tooltip opens */
  onOpen: PropTypes.func,
  /** Spring animation component */
  springAnimationComponent: PropTypes.element,
  /** Whether to show an arrow or not */
  showArrow: PropTypes.bool,
}

export { TooltipType }
export default DSTooltip
